.homeContainer {
  width : 100%;
  height: 100%;
  position: relative;
}
.homeContainer .button{
  margin-top :20px;
}
/* 홈메인 */
.homeMain{
  width : 100%;
  height: 60vw;
  background :url(https://www.marshallheadphones.com/dw/image/v2/BCQL_PRD/on/demandware.static/-/Library-Sites-SharedLibrary-Marshall/default/dw31fb25da/images/front-page/year/2021/january/post-holiday/post-holiday-hero-emberton-bnb-en-big-desktop@1.41x.jpg?sw=2000&sh=2000&sm=fit)no-repeat;
  background-size : 100% 100%;
  cursor: pointer;
  position: relative;
}
.homeMain .gradientDiv{
  width : 100%;
  height: 60vw;
  transition: background 0.6s;
}
.homeMain .gradientDiv:hover{
  background: rgb(0, 0, 0, 0.15);
}
.homeWrap{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: absolute;
  bottom: 6vw;
  left : 50%;
  transform: translateX(-50%);
}
.mainText {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 3;
}

.mainText a{
 color : white;
 font-weight: bold;
 padding : 1vw;
}
.mainText #text1{
  font-size : 1.25vw;
}
.mainText #text2{
  font-size : 3.25vw;
  letter-spacing: -0.3vw;
}
.mainText #text3{
  font-size : 1.1vw;
}
/* 홈메인 */

/* 홈컨텐츠01*/
.homeContents01{
  width : 100%;
  height: 40vw;
  background :#232225;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.homeContents01 .contentsDiv01{
  width : 85%;
  height: 42vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
}
.homeContents01.contentsDiv01 a{
  width : 80%;
  margin : 0 2vw;
}
.homeContents01 .contentsDiv01 a .gradientDiv {
  width : 100%;
  height: 100%;
  transition: background 0.6s;
}
.homeContents01 .contentsDiv01 .gradientDiv:hover{
  background: rgb(0, 0, 0, 0.2);
}
.homeContents01 .contentsDiv01 .imgDiv01{
  width : 45%;
  height: 37vw;
  background : url(https://www.marshallheadphones.com/dw/image/v2/BCQL_PRD/on/demandware.static/-/Library-Sites-SharedLibrary-Marshall/default/dwdadeb577/images/front-page/year/2021/january/post-holiday/post-holiday-2-col-acton-ii-bt-desktop@1.41x.jpg?sw=1234&sh=2000&sm=fit);
  background-size : 100% 100%;
  position : relative;
}
.homeContents01 .contentsDiv01 .contentsText {
  width:  100%;
  height: 10vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  bottom : 2vw;
}
.homeContents01 .contentsDiv01 span{
  color : white;
  font-size : 1vw;
  margin-bottom: 10px;
  font-weight: bold;
}
.homeContents01 .contentsDiv01 h2{
  color : white;
  font-size : 2vw;
  font-weight: bold;
}
.homeContents01 .contentsDiv01 .imgDiv02{
  width : 45%;
  height: 37vw;
  background : url(https://www.marshallheadphones.com/dw/image/v2/BCQL_PRD/on/demandware.static/-/Library-Sites-SharedLibrary-Marshall/default/dwdfa94768/images/front-page/year/2021/january/post-holiday/post-holiday-2-col-acton-ii-bt-white-desktop@1.41x.jpg?sw=1234&sh=2000&sm=fit);
  background-size : 100% 100%;
  position : relative;
}
/* 홈컨텐츠01 end*/

